footer {
    background-color: #111 ;
    border-top: 7px solid #333;
    position: absolute;
    left: 0;
    width: 100%;
    box-sizing: border-box;
}

.contactus {
    padding: 70px 30px 20px;
}

.socialmedia {
    display: flex;
    justify-content: center;
}

.socialmedia a{
    text-decoration: none;
    padding: 10px;
    padding-bottom: 5px;
    background-color: white;
    color: black;
    margin: 10px;
    border-radius: 50%;
}

.socialmedia a .icons {
    font-size: 2em;
    opacity: 0.9;
}

.socialmedia a:hover {
    color: white;
    background-color: transparent;
    transition: 0.5s;
}

.footerNav {
    margin: 30px 0;
}

.footerNav ul {
    display: flex;
    list-style: none;
    justify-content: center;
}

.footerNav ul li a {
    color: white;
    margin: 20px;
    text-decoration: none;
    font-weight: 600;
    font-size: 1.3em;
    opacity: .7;
    transition: .5s;
}

.footerNav ul li a:hover {
    opacity: 1;
}

.footerBottom {
    background-color: #000;
    padding: 20px;
    text-align: center;
}

.footerBottom p {
    color: white;
    font-weight: 600;
}

#designer {
    opacity: 0.7;
    letter-spacing: 1px;
    font-weight: 700;
    margin: 0px 5px;
}
